/*Header Start*/
.header-image {
  width: 100%;
  overflow: hidden;
  margin-top: 110px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: -50px;
}

.header-image img {
  width: 100%;
  height: auto;
  max-width: 1000px;
  margin: auto;
  display: block;
}

.header-image::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 0%,
    #f9f9f9 100%
  );
  filter: blur(-50px);
  pointer-events: none;
}
/*Header End*/

.content {
  margin-top: -50px;
  padding: 20px;
}

.topics {
  text-align: center;
  padding: 20px 0;
  margin-top: -20px;
}

.topics h1 {
  font-size: 1.3em;
  margin-bottom: 20px;
  color: #333;
}

.topic-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.topic-item {
  background: white;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 10px 15px;
  text-align: center;
  width: 140px; /* Smaller width for the topic items */
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
}

.topic-item:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.topic-item i {
  font-size: 2em; /* Larger icons */
  color: #333;
  margin-bottom: 10px;
}

.topic-item p {
  font-size: 0.9em;
  color: #000000;
  font-weight: bold;
}

.search-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}

#topic-search {
  width: 70%;
  padding: 15px 20px;
  padding-right: 50px;
  font-size: 16px;
  border: 2px solid rgba(200, 200, 200, 0.3);
  border-radius: 30px;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  outline: none;
  transition: all 0.3s ease;
}

#topic-search:focus {
  border-color: rgba(52, 152, 219, 0.5);
  box-shadow: 0 4px 20px rgba(52, 152, 219, 0.15);
  background: rgba(255, 255, 255, 0.95);
}

.search-button {
  position: absolute;
  right: calc(15% + 10px);
  background: transparent;
  border: none;
  padding: 10px;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.search-button i {
  color: #666;
  font-size: 18px;
  transition: color 0.2s ease;
}

.search-button:hover {
  transform: scale(1.1);
}

.search-button:hover i {
  color: #3498db;
}

@media only screen and (min-width: 481px) and (max-width: 767px) {
  .search-button {
    right: 5%;
  }
}

@media (max-width: 768px) {
  .search-button {
    right: 7%;
  }

  #topic-search {
    width: 90%; 
  }

  .search-icon {
    right: 33px; 
  }
}

@media (min-width: 769px) {
  .header-image img {
    width: 950px;
    margin: 0 auto;
  }

  .topics {
    margin-top: 0px;
  }
}

@media (max-width: 768px) {
  .header-image img {
    max-width: 1800px;
    width: 600px;
  }

  .topic-item {
    width: 130px;
    padding: 8px 10px;
  }

  .topic-item i {
    font-size: 1.8em;
  }

  .topic-item p {
    font-size: 0.8em;
  }

  header {
    padding: 10px;
  }

  .logo {
    width: 50px;
    height: 50px;
  }

  .pdf-item {
    width: 110px;
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .header-image img {
    max-width: 1800px;
    width: 600px;
    height: auto;
  }

  .topic-list {
    gap: 10px;
  }

  .topic-item {
    width: 22%; 
    padding: 8px 0px;
  }

  .topic-item i {
    font-size: 1.5em;
  }

  .topic-item p {
    font-size: 0.6em;
  }
}
